import React, { Component } from 'react';
import HomeList from './HomeList';
import '../App.css';
import AV from 'leancloud-storage';

export default class HomePage extends Component {

  getItem() {
     var strategy = AV.Object.createWithoutData('Strategy', '5a27b65aee920a004474381d');
     strategy.fetch().then(function () {
       var title = strategy.get('title');// 读取 title
       var desc = strategy.get('desc');// 读取 content
       console.log(title + desc);

       this.state = {
         stra: strategy
       }
     }, function (error) {
       // 异常处理
     });
   }

  render() {
    return (
      <div className="Page">

        <HomeList />

      </div>


    );
  }
}



