import React, { Component } from 'react';
import './home_item.css';
import AV from 'leancloud-storage';
import {Link,Route,BrowserRouter as Router} from 'react-router-dom'

export default class Item extends Component {

  render() {

    return (

        <a className="AStyle"><Link to={"/detail/" + this.props.strategy.get("objectId")}>
          <li>
            <div className="HomeItem">

              <div className="HomeItemTop">

                <img className="Avatar" src={this.props.strategy.get("user").get("avatar").get("url")}></img>
                <div className="Verti">
                  <div className="UserName">{ this.props.strategy.get("user").get("nick") }</div>
                  <div className="Sign">{ this.props.strategy.get("user").get("sign") }</div>
                </div>
              </div>

              <div className="CoverDiv">

                <img className="Cover" src={this.props.strategy.get("cover").get("url")}></img>
              </div>

              <div>

                <div className="StrategyTitle">{this.props.strategy.get("title")}</div>
                <div className="SubTitle">{this.props.strategy.get("subtitle")}</div>
                <div className="Line"></div>
                <div className="Bottom">
                  <div className="TopicDesc">栏目</div>
                  <div className="Topic">
                    {this.props.strategy.get("topic").get("title")}
                  </div>
                </div>
              </div>



              <div>


              </div>

            </div>

          </li>
        </Link>
      </a>
    );
  }
}



