import React, { Component } from 'react';
import '../App.css';
import AV from 'leancloud-storage';
import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom';
import './strategy_detail.css';
import '../App.css';

export default class StrategyDetail extends Component {


  constructor(props) {
    super(props);
    var id = props.match.params.id;
    this.state = {
      items: new Array(),
      strategy: null,
    };
  }

  componentDidMount() {
    this.getDetail();
  }

  getDetail = () => {
    var todo = AV.Object.createWithoutData('Strategy', this.props.match.params.id);
    todo.fetch().then(() => {
      var title = todo.get('title');// 读取 title
      this.getItems();
      this.setState({
          strategy: todo
      });

    }, function (error) {
      // 异常处理
    });
  }

  getItems = () => {
    var query = new AV.Query('StrategyItem');
    query.include("user");
    // query.include("topic");
    query.find().then( (items) => {
      this.setState({
          items: items
      });
    }, function (error) {
        // 异常处理
    });
  }

  getHeader = () => {
    console.log(this.state.strategy);
    if (this.state.strategy) {
      return (<div>
        <img className="Cover" src={this.state.strategy.get("cover").get("url")}></img>
        <div className="DetailTitle">{this.state.strategy.get("title")}</div>
        <div className="Desc">{this.state.strategy.get("desc")}</div>
      </div>);
    }
    else {
      return <div>攻略不存在</div>
    }
  }

  render() {
    const header = this.getHeader();

    const list = this.state.items.map((item) =>
      <div>
        <div className="DetailTitle">{item.get("title")}</div>
        <div className="Desc">{item.get("desc")}</div>
      </div>
    );
    return (
      <div className="Page">
        {header}
        {list}
      </div>
    );
  }
}



