import React, { Component } from 'react';
import './home_item.css';
import AV from 'leancloud-storage';
import Item from './Item';

export default class HomeList extends Component {

  constructor(props) {
      super(props);
      this.state = {strategyList: new Array()};
      this.getItem();
    }

    getItem = () => {

      var query = new AV.Query('Strategy');
      query.include("user");
      query.include("topic");
      query.find().then( (strategys) => {
        this.setState({
            strategyList: strategys
        });
      }, function (error) {
          // 异常处理
      });

     }

  render() {

    const list = this.state.strategyList.map((obj) =>
      <Item strategy={obj} />
    );

    // console.log(list);

    return (

      <ul>
        {list}
      </ul>


    );
  }
}



